@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{border:none;margin:0;padding:0}a{text-decoration:none !important}html,body{width:100%;height:100%;font-family:"Lora",serif}ul li{list-style:none}header{max-height:105px;background-color:#f3f3f3}header .header-logo{display:inline-block;line-height:105px}header .header-nav{display:flex;line-height:105px;justify-content:flex-end}header .header-nav .main-nav{display:flex;position:relative}header .header-nav .main-nav .main-nav_item{display:flex;margin-right:45px}header .header-nav .main-nav .main-nav_item .main-nav_item--link{text-transform:uppercase;font-family:"Montserrat",sans-serif;font-size:12px;font-weight:600;color:#1b1b1b;font-style:normal;font-style:initial;line-height:105px}header .header-nav .main-nav .main-nav_item i{margin-left:5px;font-size:9px;font-weight:lighter;line-height:105px}header .header-nav .main-nav .main-nav_item:hover .main-nav_item--link{color:#519f10}header .header-nav .main-nav .main-nav_item:last-child{margin-right:0}header .header-nav .header-currency{height:105px;cursor:pointer;padding-left:62px;padding-right:10px;display:flex;position:relative}header .header-nav .header-currency span{font-family:"Montserrat",sans-serif;font-size:12px;font-weight:600;color:#1b1b1b}header .header-nav .header-currency i{font-size:10px;display:inline-block;margin-left:8px;font-weight:lighter;line-height:105px}header .header-nav .header-currency::before{display:block;content:"";width:1px;height:16px;border-left:1px solid #1b1b1b;position:absolute;top:44%;left:25%}header .header-nav .header-cart{margin-left:20px;height:105px;position:relative;cursor:pointer}header .header-nav .header-cart span{position:absolute;bottom:16%;text-align:center;font-size:13px;width:18px;color:#519f10}header .header-nav .header-cart i{font-size:20px;font-weight:500}@media screen and (max-width: 1100px){header .header-nav .main-nav .sub-nav .column{padding-left:10px}}

